@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*::-webkit-input-placeholder {
  color: gray;
  opacity: 1; }

*:-moz-placeholder {
  color: gray;
  opacity: 1; }

*::-moz-placeholder {
  color: gray;
  opacity: 1; }

*:-ms-input-placeholder {
  color: gray;
  opacity: 1; }

body input:focus:required:invalid,
body textarea:focus:required:invalid {
  color: #666; }

body input:required:valid,
body textarea:required:valid {
  color: #666; }

input, textarea {
  box-shadow: none;
  border-radius: 0;
  -webkit-appearance: none;
  outline: none; }

html {
  font-size: 16px; }

body {
  background-color: red;
  position: relative;
  min-width: 270px;
  font-family: "Ubuntu", sans-serif;
  color: #000;
  line-height: 1.2;
  overflow-x: hidden;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

.max-width {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px; }
  @media only screen and (min-width: 768px) {
    .max-width {
      padding-left: 30px;
      padding-right: 30px; } }

.clearfix:after {
  content: '';
  display: table;
  clear: both; }

address, blockquote, center, section, main, header, footer, div, fieldset, form, input, textarea,
h1, h2, h3, h4, h5, h6, hr, menu, ol, ul, li, p, span, a, pre, table {
  box-sizing: border-box; }

a {
  text-decoration: none; }

@keyframes animat-btn {
  0% { }
  100% { } }

#block-menu {
  position: relative;
  min-height: 54px;
  padding-top: 54px; }
  @media only screen and (min-width: 768px) {
    #block-menu {
      padding-top: 15px; } }

.menu {
  display: none; }
  @media only screen and (min-width: 768px) {
    .menu {
      display: flex !important; } }

.active-menu {
  display: block; }

.has-submenu {
  position: relative;
  padding-right: 30px; }
  .has-submenu:before {
    content: '';
    position: absolute;
    right: 0;
    top: 2px;
    width: 10px;
    height: 10px;
    background: url("../img/prev-next.svg");
    background-size: contain;
    transition: 0.2s; }
  .has-submenu:hover:before {
    transform: rotate(180deg); }

.submenu {
  display: none; }
  @media only screen and (min-width: 768px) {
    .submenu {
      position: absolute; } }

/************mob-btn*************/
.mob-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 32px;
  height: 24px;
  cursor: pointer; }
  @media only screen and (min-width: 768px) {
    .mob-btn {
      display: none; } }
  .mob-btn .icon-bar {
    position: absolute;
    margin: 0;
    width: 100%;
    height: 16.67%;
    border-radius: 2px;
    background-color: #000;
    transform: rotate(0deg);
    transition: 0.1s ease-in-out; }
  .mob-btn .icon-bar:first-child {
    top: 0; }
  .mob-btn .icon-bar:nth-child(2) {
    top: 50%;
    transform: translateY(-50%); }
  .mob-btn .icon-bar:nth-child(3) {
    top: 50%;
    transform: translateY(-50%); }
  .mob-btn .icon-bar:last-child {
    bottom: 0; }

.active-mob-btn .icon-bar:first-child,
.active-mob-btn .icon-bar:last-child {
  opacity: 0; }

.active-mob-btn .icon-bar:nth-child(2) {
  transform: rotate(45deg); }

.active-mob-btn .icon-bar:nth-child(3) {
  transform: rotate(-45deg); }

.btn {
  width: 100px;
  height: 100px; }

.tabs__content {
  display: none; }

.tabs__content.active {
  display: block; }

.tabs__caption {
  display: flex;
  flex-flow: wrap;
  margin-bottom: 20px;
  padding-top: 20px; }
  .tabs__caption li {
    color: #555;
    cursor: pointer;
    letter-spacing: -0.5px;
    margin-left: 9px;
    margin-right: 9px; }
  .tabs__caption .active {
    position: relative; }
    .tabs__caption .active:before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px; }

